export default {
  name: 'build',
  data () {
    return {
      token: '',
      roomId: '',
      num: 0,
      owner: 0,
      isSigned: false,
      showSign: false,
      planBuildList:[
        {path: '#', icon: 'https://img1.aylives.cn/7dc8afdf26474cd493ea599d1b1e4cc0.png', content: '', rule: '+3', cityType: ''},
        {path: '#', icon: 'https://img1.aylives.cn/c9218154e0124ea2a4e756f1a7b7adeb.png', content: '', rule: '+3', cityType: ''},
      ]
    }
  },
  created () {
  },
  mounted () {
  },
  methods: {
    getSteps() {
      let donateUrl = "https://h5.aylives.cn/points/#/donateSteps"
      if (Util.isIos()) {
        window.webkit.messageHandlers.openDonateStepH5.postMessage({donateUrl: donateUrl})
      } else if (Util.isAndroid()) {
        window.android.openDonateStepH5(donateUrl)
      } else {
        this.$toast("", 1500)
      }
    },
    tips() {
      this.$toast("", 1500)
    },
  }
}



