import Vue from 'vue'
import Router from 'vue-router'
// 
import index from '@/components/index'
import rank from '@/components/rank'
import explain from '@/components/explain'

// 
import points from '@/components/points/index'
import record from '@/components/points/record'
import share from '@/components/points/share/index'
import sharePage from '@/components/points/share/sharePage'
import donateSteps from '@/components/points/donateSteps'
import signIn from '@/components/points/signIn'
import attention from '@/components/points/attention'

// 
import personalCenter from '@/components/castle/index'
import detail from '@/components/castle/detail'

// 
import build from '@/components/build/index'
// 
// 
import exchange from '@/components/build/exchange/index'
// 
import push from '@/components/build/exchange/push'
// 
import exchangeProduct from '@/components/build/exchange/exchange'
// 
import publish from '@/components/build/exchange/publish'
// 
import productDetail from '@/components/build/exchange/productDetail'
// 
import receive from '@/components/build/exchange/receive'

// 
import store from '@/components/store/index'

// import model from '@/components/public/model'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/index',
      name: 'index',
      component: index
    },
    {
      path: '/rank',
      name: 'rank',
      meta: {
        title: ''
      },
      component: rank
    },
    {
      path: '/explain',
      name: 'explain',
      meta: {
        title: ''
      },
      component: explain
    },
    {
      path: '/points',
      name: 'points',
      meta: { title: '' },
      component: points,
    },
    {
      path: '/record',
      name: 'record',
      meta: {
        title: ''
      },
      component: record
    },
    {
      path: '/store',
      name: 'store',
      meta: {
        title: ''
      },
      component: store
    },
    {
      path: '/personalCenter',
      name: 'personalCenter',
      meta: {
        title: ' '
      },
      component: personalCenter
    },
    {
      path: '/detail',
      name: 'detail',
      meta: {
        title: ''
      },
      component: detail
    },
    {
      path: '/donateSteps',
      name: 'donateSteps',
      meta: {
        title: ''
      },
      component: donateSteps
    },
    {
      path: '/signIn',
      name: 'signIn',
      meta: {
        title: ''
      },
      component: signIn
    },
    {
      path: '/attention',
      name: 'attention',
      meta: {
        title: ''
      },
      component: attention
    },
    {
      path: '/share',
      name: 'share',
      meta: {
        title: ''
      },
      component: share
    },
    {
      path: '/sharePage',
      name: 'sharePage',
      meta: {
        title: ''
      },
      component: sharePage
    },
    {
      path: '/build',
      name: 'build',
      meta: {
        title: ''
      },
      component: build
    },
    {
      path: '/exchange',
      name: 'exchange',
      meta: {
        title: ''
      },
      component: exchange,
    },
    {
      path: '/push',
      name: 'push',
      meta: {
        title: ''
      },
      component: push,
    },
    {
      path: '/exchangeProduct',
      name: 'exchangeProduct',
      meta: {
        title: ''
      },
      component: exchangeProduct,
    },
    {
      path: '/productDetail',
      name: 'productDetail',
      meta: {
        title: ''
      },
      component: productDetail
    },
    {
      path: '/receive',
      name: 'receive',
      meta: {
        title: ''
      },
      component: receive,
    },
    {
      path: '/publish',
      name: 'publish',
      meta: {
        title: ''
      },
      component: publish,
    }
  ]
})

router.beforeEach((to, from, next) => {
  /* title */
  if (to.meta.title) {
    document.title = to.meta.title
  }
  
  if (/\/http/.test(to.path)) {
    let url = to.path.split('http')[1]
    window.location.href = `http${url}`
  } else {
    next()
  }
})

export default router



